<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
</style>

<template>
  <div>
    <el-container>
    <el-aside width="210px" height="700px">
      <el-menu :default-active="$route.path"  :router="true" class="el-menu-vertical-demo" @select="route" >
        <el-menu-item align="left" index="admin"><i class="el-icon-menu"></i><span slot="title"></span></el-menu-item>
        <el-menu-item index="password" align="left"><i class="el-icon-key"></i><span slot="title"></span></el-menu-item>
        <el-submenu index="2" v-show="user_deter" align="left">
          <template slot="title"><i class="el-icon-user"></i><span slot="title"></span></template>
          <el-menu-item-group>
            <el-menu-item align="left" index="user"></el-menu-item>
            <el-menu-item align="left" index="group"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="3" v-show="product_deter" align="left">
          <template slot="title"><i class="el-icon-box"></i><span slot="title"></span></template>
          <el-menu-item index="product" align="left"></el-menu-item>
          <el-menu-item index="option" align="left"></el-menu-item>
          <el-menu-item index="product_group" align="left"></el-menu-item>
        </el-submenu>
        <el-menu-item index="region" v-show="region_deter" align="left"><i class="el-icon-map-location"></i><span slot="title"></span></el-menu-item>
        <el-menu-item index="Show_All" align="left"><i class="el-icon-back"></i><span slot="title"></span></el-menu-item>
        <el-menu-item index="login" align="left"><i style="color:#F56C6C" class="el-icon-switch-button"></i><span style="color:#F56C6C" slot="title"></span></el-menu-item>
      </el-menu>
    </el-aside>
      <el-link type="success" style="position:absolute;right:80px;top:32px;" icon="el-icon-s-custom">{{user[0]}}</el-link>
    </el-container>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        user:[],
        user_id:'',
        user_deter:false,
        product_deter:false,
        region_deter:false,
      };
    },
    created(){
      this.user = JSON.parse(sessionStorage.getItem('user'))//user
      this.axios.get("/admin_user",{
        }).then((response)=>{
          this.all_Data = JSON.parse(JSON.stringify(response.data));//all_Data
          this.user_data = JSON.parse(JSON.stringify(this.all_Data['user_data']))
          for(let user_compare of this.user_data)
             if(user_compare.user == this.user[0])
               this.user_id = user_compare.user_id;

          var operator_authority = new Set();
          for(let group of this.all_Data['group_data']){// 
            if(group.members.indexOf(String(this.user_id)) != -1){
              if(group.authority != '')
                if(JSON.parse(group.authority)['operator'] != undefined && JSON.parse(group.authority)['operator'] != [])
                  for(let operator of JSON.parse(group.authority)['operator'])
                    operator_authority.add(operator)
            }
          }
          var authority = []
          if(operator_authority.has('')){
            this.user_deter = true
            authority.push('')
          }
          if(operator_authority.has('')){
            this.product_deter = true
            authority.push('')
          }
          if(operator_authority.has('')){
            this.region_deter = true
             authority.push('')
          }
          if(operator_authority.has(''))
            authority.push('')
          sessionStorage.setItem("authority",JSON.stringify(authority));//
        });
    },
    methods: {
      route(key, keyPath) {//keynullLogout
        if(key == 'login'){
          sessionStorage.clear();
        }
      },
    }
  }
</script>