<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#note{
    display:inline;
}
.noedit-input /deep/ .el-input__inner{
  background-color:#FF9797; 
}
.input_require input.el-input__inner {
    border:#FF7575 1px solid;
}
</style>
<template>
    <div>
        <div style="position:relative;bottom:20px;font-size:28px;"></div>
        <div :data="sub_row">
            <div align="left" id = 'left100px'>
                ():<el-input :class="deter_require == 'customer'?'input_require':''" style="position:relative;left:32px;width:110px;"  size='small' v-model="input.name" disabled placeholder=""></el-input>
                <span style="position:relative;left:50px;">():</span>
                <el-input style="position:relative;left:50px;width:100px" size='small' v-model="input.factory" disabled placeholder=""></el-input>
                <span style="position:relative;left:500px;font-size:16px;bottom:5px;">  - (3)</span>
                <span style="position:relative;left:328px;top:21px;">  key </span>
                <span style="position:relative;left:25px;top:42px;"> </span>
            </div>
            <div align="left" id = 'left100px'>
                KeyID():<el-input  :class="deter_require == 'sn'?'input_require':''" v-model="sub_row.sn" style="position:relative;left:20px;width:110px;" size='small' placeholder="KeyID" disabled></el-input>
                <span style="position:relative;left:40px;">():</span>
                <el-select :class="deter_require == 'type'?'input_require':''" size="small" v-model="sub_row.type" style="position:relative;left:40px;width:100px" @change="type_change" placeholder="">
                    <el-option
                        v-for="item in type_opt" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
                <!--<el-autocomplete style="position:relative;left:40px;width:100px"  size='small' v-model="sub_row.type" 
                        :fetch-suggestions="querySearch_type" placeholder=""></el-autocomplete>-->
            </div>
            <div align="left" id = 'left100px'>():
            <!--<el-autocomplete :class="deter_require == 'region'?'input_require':''" style="width:110px"  size='small' v-model="sub_row.region":fetch-suggestions="querySearch_region" placeholder=""></el-autocomplete>-->
            <el-select :class="deter_require == 'region'?'input_require':''" style="position:relative;right:5px;width:110px"  size='small' v-model="sub_row.region" placeholder="">
                <el-option
                    v-for="item in region_opt" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            </div>
            <div align="left" id = 'left100px'>
            ():<el-input  :class="deter_require == 'caption'?'input_require':''" style="width: 150px"  size='small' v-model="sub_row.caption" 
                        placeholder="" disabled></el-input>
                        <!--<el-autocomplete ref="pro_caption" style="width: 150px"  size='small' v-model="sub_row.caption" 
                        :fetch-suggestions="querySearch_caption" placeholder="" readonly></el-autocomplete>-->
            <span style="position:relative;left:10px;">
            ():</span><el-input  :class="deter_require == 'func_uid'?'input_require':''" style="width:250px;position:relative;left:10px"  size='small' v-model="sub_row.func_uid" 
                       placeholder="" disabled></el-input>
                        <!--<el-autocomplete ref="pro_name" style="width:180px;position:relative;left:10px"  size='small' v-model="sub_row.func_uid" 
                        :fetch-suggestions="querySearch_func_uid(sub_row)"placeholder="" readonly></el-autocomplete>-->
                <span style="position:relative;left:241px;bottom:23px;">  - (6)</span>
                <span style="position:relative;left:22px;top:2px;"> ()</span>
                <span style="position:relative;right:259px;top:26px;bottom:1px;"> </span>
            </div>
            <div align="left" id = 'left100px'>():<el-input :class="deter_require == 'count'?'input_require':''" v-model="sub_row.count" style="width:100px;position:relative;left:32px;" size = "small" placeholder=""></el-input></div>
            <div align="left" id = 'left100px'>
            ():<el-date-picker :class="deter_require == 'expiration'?'input_require':''" type="date" value-format="yyyy-MM-dd" name="expiration" v-model="sub_row.expiration" style="width:200px;position:relative;left:16px;" placeholder=""></el-date-picker>
            <span style="position:relative;left:32px;">():</span>
            <el-input v-model="sub_row.version" style="width:200px;position:relative;left:32px;" size = "small" placeholder=""></el-input>
                <span style="position:relative;bottom:5px;left:262px;">  - (131)</span>
                <span style="position:relative;left:23px;top:20px"> </span>
                <!--<span style="position:relative;right:328px;top:43px;"> </span>-->
            </div>
            <div align="left" id = 'left100px'>
            ():
                <!--<el-input :class="deter_require == 'user'?'input_require':''" v-model="sub_row.user" style="width:200px;position:relative;left:16px;" size = "small" placeholder=""readonly></el-input>-->
                <template  v-for="(item, index) in sub_row.user_display">
                    <el-autocomplete :class="deter_require == 'user'?'input_require':''" v-model="sub_row.user_display[index]" style="width:200px;position:relative;left:12px;" :fetch-suggestions="querySearch_user" placeholder="" ></el-autocomplete>
                    <el-button v-show="!lock[row_index] || lock.length != 0 && index != 0" type="text" icon="el-icon-error" style="color:#F56C6C;font-size:15px;position:relative;bottom:15px;left:3px;" @click="remove_user(index)"></el-button>
                </template>
                <el-button v-show="!lock[row_index] || lock.length != 0" type="text" icon="el-icon-circle-plus" style="font-size:15px;" @click="add_user()"></el-button>
            </div>
            <div align="left" id = 'left100px' >
            ():
                <template  v-for="(item, index) in sub_row.contact_display">
                    <el-autocomplete :class="deter_require == 'contact'?'input_require':''" v-model="sub_row.contact_display[index]" style="width:200px;position:relative;left:12px;" :fetch-suggestions="querySearch_contact" placeholder="" ></el-autocomplete>
                    <el-button v-show="!lock[row_index] || lock.length != 0 && index != 0" type="text" icon="el-icon-error" style="color:#F56C6C;font-size:15px;position:relative;bottom:15px;left:3px;" @click="remove_contact(index)"></el-button>
                </template>
                <el-button v-show="!lock[row_index] || lock.length != 0" type="text" icon="el-icon-circle-plus" style="font-size:15px;" @click="add_contact()"></el-button>
            </div>
            <div align="left" id = 'left100px'>():<el-input v-model="sub_row.comment" style="width:200px;position:relative;left:32px;" size = "small" placeholder=""></el-input>
                <span style="position:relative;bottom:40px;left:589px;"> </span>
                <span style="position:relative;left:562px;bottom:13px;"> </span>
                <span style="position:relative;left:372px;bottom:74px;"> </span>
            </div>
        </div>
        <div align = "center" style="position:relative;top:20px;">
            <el-button style="position:relative;left:550px;width:150px;display:inline;" @click="cancel" size = "medium" v-show="lock[row_index] || lock.length == 0"></el-button>
            <el-button style="position:relative;left:550px;width:150px;display:inline;" @click="submit" size = "medium" v-show="lock[row_index] || lock.length == 0"></el-button>
            <el-button style="position:relative;right:280px;bottom:45px;" circle type="primary" icon="el-icon-check" @click="store_row()" medium v-show="!lock[row_index] && lock.length != 0"></el-button>
            <el-button style="position:relative;right:280px;bottom:45px;" circle type="danger" icon="el-icon-close" @click="cancel_row()" medium v-show="!lock[row_index] && lock.length != 0"></el-button>
            <el-table
            :row-class-name="tableRowColor"
            @selection-change="handleSelectionChange"
            ref="edittable"
            :data="tableData"
            style="width: 100%">
            <el-table-column type="selection"align="center" prop="" label=""  width="50"></el-table-column>
            <el-table-column align="center" prop="" label=""  width="100">
                <template slot-scope = "{$index,row}" >
                    <el-button circle type="primary" icon="el-icon-refresh-right" @click="restore($index,row)" size = "mini" v-show="lock[$index]"></el-button>
                    <el-button circle type="primary" icon="el-icon-edit" @click="edit_row($index,row)" size = "mini" v-show="lock[$index]"></el-button>
                    <!--<el-button circle type="primary" icon="el-icon-check" @click="store_row($index,row)" size = "mini" v-show="!lock[$index]"></el-button>
                    <el-button circle type="danger" icon="el-icon-close" @click="cancel_row($index,row)" size = "mini" v-show="!lock[$index]"></el-button>-->
                </template>
            </el-table-column>
            <el-table-column align="center" prop="customer" label="" width="140">
                <template slot-scope = "{row}" >
                    <el-input type="text" name="customer" v-model="row.customer" readonly style="width: 110px"></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="sn" label="KeyID" width="120"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="sn" v-model="row.sn" readonly style="width: 100px"></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="func_uid" label="" width="190"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="func_uid" v-model="row.func_uid" style="width: 160px" readonly></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="version" label="" width="100"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="version" v-model="row.version" style="width: 80px" readonly ></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="count" label="" width="80"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="count" v-model="row.count" style="width: 50px" readonly ></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="expiration" label="" width="170"> 
                <template slot-scope = "{row}" >
                    <el-date-picker type="date" value-format="yyyy-MM-dd" name="expiration" v-model="row.expiration" style="width: 140px;" readonly ></el-date-picker>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="type" label="" width="100"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="type" v-model="row.type" style="width: 80px" readonly ></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="user" label="" width="80"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="user" v-model="row.user" style="width: 60px" readonly></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="region" label="" width="100"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="region" v-model="row.region" style="width: 70px" readonly ></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="comment" label="" width="160"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="comment" v-model="row.comment" style="width: 130px" readonly></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="contact" label="" width="100"> 
                <template slot-scope = "{row}" >
                    <el-input type="text" name="contact" v-model="row.contact" style="width: 80px" readonly></el-input>
                </template>
            </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
export default {
  name: 'edit',
  data: function() {
        return { 
            all_Data:{},//
            tableData:[],
            func_uid_Data:[],//(==origin_Data)
            origin_Data:[],//()
            checked_Data:[],//
            user_opt:[],
            region_opt:[],
            caption_opt:[],
            func_uid_opt:[],
            contact_opt:[],
            type_opt:[{label: '',value: ''},
                    {label: '',value: ''},
                    {label: '',value: ''},
                    {label: '',value: ''}],
            
            word:":                                   \n"+
                 ":  ()     \n"+
                 ":  ()     \n"+
                 ":  ( KeyID )\n"+
                 ":                 ",
            input:{//input()
                name:'',
                factory:''
            },
            sub_row:{
                caption: "",
                comment: "",
                contact: "",
                count: null,
                customer: "",
                expiration: "",
                func_uid: "",
                index: null,
                info: [],
                info_id: null,
                issued: "",
                operator: "",
                region: "",
                registration: "",
                sn: null,
                type: "",
                user: "",
                version: "",
                contact_display:[''],
                user_display:['']},
            lock:[],
            row_index:0,
            deter_require:'',
        }
  },
  created(){
      this.user = JSON.parse(this.$route.query.user);//user
      this.all_Data = JSON.parse(this.$route.query.all_Data);
      for (let caption of this.all_Data['product_data']){
        this.caption_opt.push({value:caption['caption'],label:caption['caption']});
      }
      for (let region of this.all_Data['region_data']){
        this.region_opt.push({value:region['name'],label:region['name']});
      }
      for (let contact of this.all_Data['user_data']){
        this.contact_opt.push({value:contact['email'],label:contact['email']});
      }
      var user_set = new Set();
      for(let user of this.all_Data['sn_data'])
        for(let user_email of user['user'].split(','))
            user_set.add(user_email)
      for(let user of user_set)
        this.user_opt.push({value:user,label:user})

      var i = 0;
      for(let check of JSON.parse(this.$route.query.checked_Data)){
        check = Object.assign(check,{'operator':'','index':i});//index rowindex
        this.func_uid_Data.push(check);
        i++;
      }
      for (let info of this.func_uid_Data){// (users) , (@)
        info['contact_display'] = info['contact'].split(',');
      }
      for(let color of this.func_uid_Data){//color
          color = Object.assign(color,{'color':0})
      }

      this.origin_Data = JSON.parse(JSON.stringify(this.func_uid_Data));
      this.tableData = JSON.parse(JSON.stringify(this.func_uid_Data));
      for(let i of this.tableData)
        this.lock.push(true);
  },
  watch:{
        tableData: function(){//tableData
            this.$nextTick(function(){ 
                for(var i = 0; i < this.tableData.length;i++){
                    if(this.tableData[i]['color'] == 1)//checked_Data  submit
                        this.$refs.edittable.toggleRowSelection(this.tableData[i],true);
                }
            }) 
        },
    },
  methods:{
      type_change(){//type
        let tempDate = new Date;
        if(this.sub_row.type == ''){//* B ==,  1/30
            tempDate.setMonth(tempDate.getMonth()+12);
            this.sub_row.expiration = tempDate.toISOString().slice(0, 4) + '-01-30'
        }
        if(this.sub_row.type == ''){//*  B ==,  3 
            tempDate.setMonth(tempDate.getMonth()+3);
            this.sub_row.expiration = tempDate.toISOString().slice(0, 10)
        }
        if(this.sub_row.type == ''){//*  B ==,  6 
            tempDate.setMonth(tempDate.getMonth()+6);
            this.sub_row.expiration = tempDate.toISOString().slice(0, 10)
        }
        if(this.sub_row.type == '')//*  B ==,  99991231
            this.sub_row.expiration = '9999-12-31'
        },
      add_user(){//
          this.sub_row.user_display.push('');
        },
      remove_user(index){//
        this.sub_row.user_display.splice(index,1);
        },
      add_contact(){//
          this.sub_row.contact_display.push('');
        },
      remove_contact(index){//
        this.sub_row.contact_display.splice(index,1);
        },
      edit_row(index, row){//row tabledata rowsub_row
        for(let i = 0; i < this.lock.length;i++)//
            if(this.lock[i] == false && i != index)
                this.cancel_row();

        if(row.customer.indexOf('|') != -1){
            this.input.name = row.customer.split('|')[0]
            this.input.factory = row.customer.split('|')[1]
        }
        else{
            this.input.name = row.customer
            this.input.factory = ''
        }
        this.row_index = index;
        this.sub_row = JSON.parse(JSON.stringify(row));
        this.$set(this.lock,index,false)
        },
      cancel_row(){// sub_row
        this.sub_row = {caption: "",comment: "",contact: "",count: null,customer: "",
            expiration: "",func_uid: "",index: null,info: [],info_id: null,issued: "",
            operator: "",region: "",registration: "",sn: null,type: "",user: "",version: "",contact_display:[''],user_display:['']}
        this.input.name = ''
        this.input.factory = ''
        this.$set(this.lock,this.row_index,true)
        },
      store_row(){
        for(let i = 0; i < this.sub_row['contact_display'].length; i++)// 
            if(this.sub_row['contact_display'][i] == ''){
                this.sub_row['contact_display'].splice(i,1);
                i--;
            }
        for(let i = 0; i < this.sub_row['user_display'].length; i++)// 
            if(this.sub_row['user_display'][i] == ''){
                this.sub_row['user_display'].splice(i,1);
                i--;
            }    
        if(this.test_edit_add()){
            if(this.tableData[this.row_index]['expiration'] != this.sub_row['expiration'] && this.tableData.length > 1){//
                this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then(() => {//
                    for(let table of this.tableData)
                        table = Object.assign(table,{'expiration':this.sub_row['expiration']})
                    this.tableData[this.row_index] = Object.assign({},this.sub_row);
                    this.tableData[this.row_index]['contact'] = this.sub_row['contact_display'].join();
                    this.tableData[this.row_index]['user'] = this.sub_row['user_display'].join();
                    this.sub_row = {caption: "",comment: "",contact: "",count: null,customer: "",
                        expiration: "",func_uid: "",index: null,info: [],info_id: null,issued: "",
                        operator: "",region: "",registration: "",sn: null,type: "",user: "",version: "",contact_display:[''],user_display:['']}
                    this.input.name = ''
                    this.input.factory = ''
                    this.datachange()
                    this.$set(this.lock,this.row_index,true)
                }).catch(() => {//
                    this.tableData[this.row_index] = Object.assign({},this.sub_row);
                    this.tableData[this.row_index]['contact'] = this.sub_row['contact_display'].join();
                    this.tableData[this.row_index]['user'] = this.sub_row['user_display'].join();
                    this.sub_row = {caption: "",comment: "",contact: "",count: null,customer: "",
                        expiration: "",func_uid: "",index: null,info: [],info_id: null,issued: "",
                        operator: "",region: "",registration: "",sn: null,type: "",user: "",version: "",contact_display:[''],user_display:['']}
                    this.input.name = ''
                    this.input.factory = ''
                    this.datachange()
                    this.$set(this.lock,this.row_index,true)
                });
            }else{//
                this.tableData[this.row_index] = Object.assign({},this.sub_row);
                this.tableData[this.row_index]['contact'] = this.sub_row['contact_display'].join();
                this.tableData[this.row_index]['user'] = this.sub_row['user_display'].join();
                this.sub_row = {caption: "",comment: "",contact: "",count: null,customer: "",
                    expiration: "",func_uid: "",index: null,info: [],info_id: null,issued: "",
                    operator: "",region: "",registration: "",sn: null,type: "",user: "",version: "",contact_display:[''],user_display:['']}
                this.input.name = ''
                this.input.factory = ''
                this.datachange()
                this.$set(this.lock,this.row_index,true)
            }
        }
        },
      test_edit_add(){//
        if(this.input.name == null || this.input.name == ""){
                this.$message({message: '',type: 'warning'});
                this.deter_require = 'customer';
                return false;
            }
        if(this.sub_row['contact_display'].length == 0){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'contact';
            return false;
        }
        if(this.sub_row['user_display'].length == 0){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'user';
            return false;
        }
        if(this.sub_row['sn'] == null || this.sub_row['sn'] == ""){
            this.$message({message: 'KeyID',type: 'warning'});
            this.deter_require = 'sn';
            return false;
        }
        if(this.sub_row['caption'] == null || this.sub_row['caption'] == ""){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'caption';
            return false;
        }
        if(this.sub_row['func_uid'] == null || this.sub_row['func_uid'] == ""){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'func_uid';
            return false;
        }
        if(this.sub_row['count'] == null || this.sub_row['count'] == ""){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'count';
            return false;
        }
        if(this.sub_row['expiration'] == null || this.sub_row['expiration'] == ""){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'expiration';
            return false;
        }
        if(this.sub_row['type'] == null || this.sub_row['type'] == ""){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'type';
            return false;
        }
        if(this.sub_row['region'] == null || this.sub_row['region'] == ""){
            this.$message({message: '',type: 'warning'});
            this.deter_require = 'region';
            return false;
        }
        let test = 0;// sn
        for(let customer of this.all_Data['customer_data']){//
            if(this.input.name == customer['name'] && this.input.factory == customer['site'])
                test = 1;
        }
        if(test == 0){
            for(let sn of this.all_Data['sn_data']){//
                if(this.sub_row['sn'] == sn['sn'] ){
                    this.$message({message: 'keyID',type: 'warning'});
                    this.deter_require = 'sn';
                    return false;
                }
            }
        }
        return true;
        },
      //select  valrow(type:array)//
      handleSelectionChange (val) {
        this.checked_Data = JSON.parse(JSON.stringify(val));
        },
      submit:function(){//
        //----------------------------------------------------//
        this.axios.post("/save_to_handle",{
            checked_Data: this.checked_Data,
            content:'none',
            user:this.user
        }).then((response)=>{
            let res = response.data;
            this.$router.push({name: 'Show_All'});
        });
        },
      cancel:function(){
          this.$router.push({name: 'Show_All'});
        },
      restore:function(index,row){//
        this.$set(this.tableData,index,this.origin_Data[index]);
        },
      datachange:function(){//  color=1
        var copy_table = JSON.parse(JSON.stringify(this.tableData))
        for(let copy of copy_table)//color1talbecolor0
            copy = Object.assign(copy,{'color':0})
        for(var i = 0; i < this.origin_Data.length; i++){
            if(JSON.stringify(this.origin_Data[i]) != JSON.stringify(copy_table[i]))
                this.tableData[i]['color'] = 1;
            else
                this.tableData[i]['color'] = 0;
          }
        this.tableData = Object.assign([], this.tableData);//watch
        },
      //------------------------------------------------------//
      tableRowColor({row, rowIndex}) {//->
        if(this.tableData[rowIndex]['color'] == 1)
            return 'exceed-row';
        },
      //-------------------el-inputfunction-------------------//
      querySearch_contact(queryString, cb){
        var results = queryString ? this.contact_opt.filter(this.createFilter(queryString)) : this.contact_opt;
